
import Blur from 'vux/src/components/blur/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import ScoreItem from '@/components/ScoreItem'
import {mapGetters, mapMutations} from 'vuex'
import {wallet} from '../config'
export default {
  head: {
    title: {
      inner: '个人中心'
    }
  },
  data () {
    return {
      logout: false,
      balance: 0,
      cumulative: 0
    }
  },
  computed: {
    ...mapGetters({
      userInfo: 'getUserInfo',
      checkAuthor: 'checkAuthor'
    })
  },
  created () {
    if (this.$localStorage.get('logined')) {
      const userId = JSON.parse(this.$localStorage.get('userInfo')).userId
      this.$http({
        method: 'jsonp',
        url: wallet,
        jsonp: 'callback',
        jsonpCallback: 'json',
        params: {
          userId: userId
        }
      })
      .then(res => {
        this.balance = res.body.data.wallet.balance
        this.cumulative = res.body.data.wallet.cumulative
        console.log(res)
      })
    }
  },
  components: {
    Blur,
    XImg,
    ScoreItem,
    Group,
    Cell,
    XButton,
    Confirm,
    Scroller
  },
  methods: {
    selectBlur () {
      if (!this.userInfo) {
        return 'static/img/blur.jpg'
      } else {
        return 'static/img/blur1.jpg'
      }
    },
    handleLogout () {
      this.$vux.toast.show({
        type: 'text',
        width: '20em',
        text: '成功退出，即将跳到登录页！',
        time: '1000'
      })
      this.postLogout(this)
    },
    ...mapMutations({
      postLogout: 'postLogout'
    })
  }
}



